<template>
     <div class="add-stu">
            <div class="add-wrapper">
                <div class="add-title">
                    <p></p>
                </div>
                <div class="add-box">
                    <div class="single">
                        <p class="add-box-title"></p>
                        <el-form :inline="true" :model="formInline" :rules="rules" ref="formInline" label-width="70px" class="demo-form-inline">
                            <el-form-item label="" prop="stuName" label-width="100px">
                                <el-input v-model="formInline.stuName" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item label="()" prop="id" label-width="100px">
                                <el-input v-model="formInline.id" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="pass" label-width="100px">
                                <el-input v-model="formInline.pass" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="classGrade" label-width="100px">
                                <el-select v-model="formInline.classGrade" filterable placeholder="" @change='changeClass'>
                                    <el-option v-for="item in classGrades" :key="item.name" :label="item.name" :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="onSubmit('formInline')"></el-button>
                            </el-form-item>
                        </el-form>
                    </div>
                    <div class="to-lead">
                        <p class="add-box-title"></p>
                        <el-upload
                            class="upload-demo"
                            drag
                            name='excel'
                            show-file-list
                            action="http://ksxt.90plus.cn/admin/user/addUserAll"
                            multiple
                            :on-preview='clickFile'
                            :on-success='uploadSucceed'>
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text"><em></em></div>
                            <!-- <div class="el-upload__tip" slot="tip">jpg/png500kb</div> -->
                        </el-upload>
                    </div>
                </div>
            </div>
        </div>
</template>
<style src='./addStu.css' scoped>
</style>
<script>
export default {
    data(){
        return {
            formInline: {classGrade: '',pass: '',id: '',stuName: ''},
            rules: {
                stuName: [
                    {required: true, message: '',trigger: 'blur'}
                ],
                id: [
                    {required: true, message: '',trigger: 'blur'}
                ],
                pass: [
                    {required: true, message: '',trigger: 'blur'}
                ],
                classGrade: [
                    {required: true, message: '',trigger: 'blur'}
                ]
            },
            classGrades: [],
            classID: 0
        }
    },
    methods: {
        onSubmit(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    const data = {
                        user_name: this.formInline.id,
                        real_name: this.formInline.stuName,
                        class_a: this.classID,
                        password: this.formInline.pass,

                    }
                    this.axios.post('/admin/user/addUser',data).then(res =>{
                        console.log(res)
                        if(res.data.code = 200){
                            this.$message({
                                message: '',
                                type: 'success'
                            })
                            this.formInline = {classGrade: '',pass: '',id: '',stuName: ''}
                        }
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        // 
        leadStu(){
            this.axios.post('/admin/user/addUserAll').then(res =>{
                console.log(res)
            })
        },
        // 
        clickFile(file){
            console.log(file)
        },
        uploadSucceed(response, file, fileList){
            console.log(response,file,fileList)
        },
        changeClass(e){
            this.classID = e;
        }
    },
    created(){
        this.axios.get('/admin/user/getClassAll').then(res =>{
            this.classGrades = res.data.data
        })
    }
}
</script>