<template>
    <div class="setup-wrapper">
         <div class="setup-box">
            <div class="search">
                <span></span>
                <div>
                    <el-select v-model="value" placeholder="" @change="changeStatus">
                        <el-option value=""></el-option>
                        <el-option value=""></el-option>
                        <el-option value=""></el-option>
                    </el-select>
                   <div class="identity">
                        <el-input v-model="identity" placeholder="" @input="searchData" clearable></el-input>
                   </div>
                </div>
                <!-- <el-button type='primary' @click="searchData" class="el-icon-search"> </el-button> -->
                <el-button type='primary' @click="addIdent('add')" class="add-ident"></el-button>
            </div>
            <div class="table">
                <p class="table-title"></p>
                <el-table :data="myTableData" style="width: 100%">
                    <el-table-column prop="index" label="" min-width="20px">
                    </el-table-column>
                    <el-table-column prop="limit_name" label="" min-width="20px">
                    </el-table-column>
                    <el-table-column prop="create_time" label="" min-width="20px">
                    </el-table-column>
                    <el-table-column prop="state" label="" min-width="20px" >
                        <template slot-scope="scope">
                            <i class="iconfont" :class="scope.row.state == 1 ? 'green' : 'red'">
                                {{scope.row.state == 1 ? '&#xe62e;' : '&#xe600;'}}
                            </i>
                        </template>
                    </el-table-column>
                    <el-table-column label="" min-width="20px">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="addIdent('redact',scope.row,scope.$index)"><i class="iconfont">&#xe64b;</i></el-button>
                            <el-button type="text" size="small" @click="deleteData(scope.row)"><i class="iconfont">&#xe631;</i></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <el-dialog :title="disTitle" :visible.sync="dialogFormVisible" :close-on-click-modal='false'>
            <el-form :model="formData">
                <el-form-item label="" label-width="80px" v-model="formData.name">
                    <el-input v-model="formData.name" placeholder="" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px">
                     <div class="block">
                        <el-cascader v-model="formData.values"
                            :show-all-levels='false'
                            placeholder=""
                            :options="options"
                            :props="{ multiple: true }"
                            filterable>
                        </el-cascader>
                    </div>
                </el-form-item>
                <el-form-item label="" label-width="80px">
                    <el-select v-model="formData.value" placeholder="">
                        <el-option value='' label=""></el-option>
                        <el-option value=',' label=","></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="addIdentTrue"> </el-button>
            </div>
        </el-dialog>
        <el-dialog
            title=""
            :visible.sync="dialogVisible2"
            width="30%">
            <span>?</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2 = false"> </el-button>
                <el-button type="primary" @click="deleteTrue"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<style scoped>
    @import './setup.css';
</style>

<script>
export default {
    data(){
        return {
            value:'',
            id: 0,
            isAdd: true,
            identity: '',
            formData:{
                name: '',
                values: [],
                value: ''
            },
            disTitle: '',
            dialogFormVisible: false,
            dialogVisible2: false,
            tableData: [
                { 
                   serial: 1,
                   identity: '' ,
                   juris: '',
                   status: false
                },
                { 
                   serial: 2,
                   identity: '' ,
                   juris: '',
                   status: true
                }
            ],
            myTableData: [],
            options: []
        }
    },
    methods: {
        changeStatus(value){
            const data = this.tableData.filter(item =>{
                return item.limit_name.match(this.identity)
            })
            if(value == ''){
                this.myTableData = data
            }else if(value == ''){
                this.myTableData = data.filter((item) =>{
                    return item.state == 0
                })
            }else if(value == ''){
                this.myTableData = data.filter((item) =>{
                    return item.state == 1
                })
            }
        },
        // /
        addIdent(value,row,index){
            this.axios.get('/admin/manage/getLimit').then(res =>{
                    console.log(res.data);
                    res.data.data.forEach((ele,index) =>{
                        this.options[index] = {}
                        this.options[index]['children'] = []
                        this.options[index]['value'] = ele.id;
                        this.options[index]['label'] = ele.menu_name;
                        ele.child.forEach((el,i) =>{
                            this.options[index]['children'][i] = {}
                            this.options[index]['children'][i]['value'] = el.id;
                            this.options[index]['children'][i]['label'] = el.menu_name;
                        })
                    })
                    this.dialogFormVisible = true;
                })
            this.formData.values = [];
            if(value == 'add'){
                this.isAdd = true;
                this.formData.value = '',
                this.formData.name = '',
                this.values = [];
                this.disTitle = ''
            }else{
                this.isAdd = false;
                this.disTitle = '';
                this.id = row.id
                console.log(this.id)
                this.axios.get('/admin/manage/modify?id='+this.id).then(res =>{
                    console.log(res);
                    this.formData.name = res.data.data.limit_name;
                    if(res.data.data.state == 0){
                        this.formData.value = ','
                    }else if(res.data.data.state == 1){
                        this.formData.value = ''
                    }
                    const data = res.data.data.limit_data;
                    const arr = []
                    data.forEach(ele =>{
                        ele.child.forEach((e) =>{
                            if(e.state == 1){
                                arr.push([ele.id,e.id])
                            }
                            
                        })
                    })
                    this.formData.values = this.formData.values.concat(arr)
                    this.dialogFormVisible = true;
                    console.log(this.formData)
                })
                
            }
        },
        // /
        addIdentTrue(){
            const data = {}
            const arr = [];
            data.limit_data = [];
            this.formData.values.forEach(ele =>{
                arr.push(ele[0])
                arr.push(ele[1])
            });
            // 
            const obj = {}
            arr.forEach((ele,index) =>{
                if(!obj[ele]){
                    data.limit_data.push(ele);
                    obj[ele] = 1
                }
            })
            data.limit_name = this.formData.name;
            if(this.formData.value == ''){
                data.state = 1
            }else{
                data.state = 0
            }
            if(this.isAdd){
                this.axios.post('/admin/manage/addIdentity',data).then(res =>{
                    this.dialogFormVisible = false;
                    this._getInfoData()
                })
            }else{
                data.id = this.id
                this.axios.post('/admin/manage/modifyIdentity',data).then(res =>{
                    this.dialogFormVisible = false;
                    this._getInfoData()
                })
            }
        },
        // 
        deleteData(row){
            this.id = row.id;
            this.dialogVisible2 = true;
        },
        // 
        deleteTrue(){
            this.axios.get('/admin/manage/deleteLimit?id='+this.id).then(res =>{
                console.log(res)
                this.dialogVisible2 = false;
                this._getInfoData()
            })
        },
        // 
        searchData(value){
            let index = 0;
            if(this.value != ''){
                if(this.value == '') index = 1;
                if(this.value == '') index = 0
                this.myTableData = this.tableData.filter(item =>{
                    return item.limit_name.match(value) && (index == item.state)
                })
            }else{
                this.myTableData = this.tableData.filter(item =>{
                    return item.limit_name.match(value)
                })
            } 
        },
        _getInfoData(){
            this.axios.get('/admin/manage/limitList').then(res =>{
                this.tableData = res.data.data;
                this.tableData.forEach((ele,index) =>{
                    ele.index = index+1
                })
                this.myTableData = this.tableData;
            })
        }
    },
    created(){
        this._getInfoData()
    }
}
</script>