<template>
    <div class="stu-wrapper">
        <div class="stu-box">
            <!-- <div class="add-stu">
                <el-form :inline="true" :model="formInline" :rules="rules" ref="formInline" label-width="70px" class="demo-form-inline">
                    <el-form-item label="" prop="name">
                        <el-input v-model="formInline.name" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                       <el-input v-model="formInline.phone" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="id">
                       <el-input v-model="formInline.id" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="sex">
                       <el-input v-model="formInline.sex" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit('formInline')"></el-button>
                    </el-form-item>
                </el-form>
            </div> -->
            <div class="search">
                <div class="condition">
                    <span></span>
                    <el-input
                        placeholder=""
                        v-model="stuSearchValue"
                        @input="searchStudent"
                        clearable>
                    </el-input>
                </div>
                <!-- <div>
                  <span class="topic-name"></span>
                  <el-input
                    placeholder=""
                    v-model="topicName"
                    @input="searchTopicName"
                    clearable>
                  </el-input>
                </div> -->
            </div>
            <div class="stu-table">
                <p class="table-title"></p>
                <template>
                    <el-table :highlight-current-row="true"
                        :data="myTableData" style="width: 100%">
                        <el-table-column fixed prop="serial" label="" min-width="5px">
                        </el-table-column>
                        <el-table-column prop="real_name" label="" min-width="10px">
                        </el-table-column>
                        <el-table-column  prop="pass" label="" min-width="10px">
                        </el-table-column>
                        <el-table-column prop="mobile" label="" min-width="10px">
                        </el-table-column>
                        <el-table-column prop="user_name" label="" min-width="20px">
                        </el-table-column>
                        <el-table-column prop="examination" label="" min-width="15px">
                        </el-table-column>
                        <!-- <el-table-column prop="teacher" label="" min-width="10px">
                        </el-table-column> -->
                        <el-table-column label="" min-width="15px">
                            <template slot-scope="scope">
                                <el-button type="text" @click="redact(scope.row)" size="small"><i class="iconfont">&#xe64b;</i></el-button>
                                <el-button type="text" @click="deleteStu(scope.row)" size="small"><i class="iconfont">&#xe631;</i></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
                <div class="paging">
                    <el-pagination
                        background
                        @current-change='currentPage'
                        @prev-click='prevPage'
                        @next-click='nextPage'
                        layout="prev, pager, next"
                        :total="pages">
                    </el-pagination>
                </div>
            </div>
        </div>
        <el-dialog title="" :visible.sync='redactBox'>
            <el-form :model="formData">
                <el-form-item label="" label-width="100px">
                    <el-input v-model="formData.real_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input type="number" v-model="formData.mobile" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input type="number" v-model="formData.user_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input type="number" v-model="formData.password" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="redactBox = false"> </el-button>
                <el-button type="primary" @click="redactTrue"> </el-button>
            </div>
        </el-dialog>
        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="deleteTrue"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<style scoped>
    @import './student.css';
</style>
<script>
export default {
    props: {
        classID: {
            type: Number
        }
    },
    data(){
        return {
            pages: 0,
            id: 0,
            stuSearchValue: '',
            dialogVisible: false,
            redactBox: false,
            formInline: {
                name: '',
                phone: '',
                id: '',
                sex: '',
                
            },
            formData: {},
            // rules: {
            //     name: [
            //         {required: true, message: '',trigger: 'blur'}
            //     ],
            //     phone: [
            //         {required: true, message: '',trigger: 'blur'}
            //     ],
            //     id: [
            //         {required: true, message: '',trigger: 'blur'}
            //     ],
            //     sex: [
            //         {required: true, message: '',trigger: 'blur'}
            //     ]
            // },
            tableData: [],
            myTableData: []
        }
    },
    methods: {
        searchStudent(value){
            this.axios.get('/admin/user/userlist?value='+value).then(res =>{
                res.data.data.forEach((ele,index) =>{
                    ele.serial = index+1
                })
                this.tableData = res.data.data
                this.pages =  Math.ceil(this.tableData.length*10/6);
                this.myTableData = this.tableData.slice(0,6)
            })
        },
        // // 
        // onSubmit(formName){
        //      this.$refs[formName].validate((valid) => {
        //         if (valid) {
        //             alert(formName);
        //         } else {
        //             console.log('error submit!!');
        //             return false;
        //         }
        //     });
        // },
        // 
        redact(row){
            this.id = row.id;
            this.redactBox = true;
            this.axios.get('/admin/user/getUserInfo?id='+this.id).then(res =>{
                this.formData = res.data.data
                console.log(res.data)
            })
        },
        // 
        redactTrue(){
            this.axios.post('/admin/user/updateUser',this.formData).then(res=>{
                console.log(res)
                this.$message({
                    message: '',
                    type: 'success'
                })
                this.redactBox = false;
                this._getStuData()
            }).catch(err =>{
                console.log(err)
            })
        },
        // 
        deleteStu(row){
            this.id = row.id;
            this.dialogVisible = true;
        },
        // 
        deleteTrue(){
            const data = {id: this.id}
            this.axios.get('/admin/user/del?id='+this.id).then(res =>{
                this.dialogVisible = false;
                this._getStuData()
            })
        },
        // 
        currentPage(index){
            this.myTableData = this.tableData.slice(index*6-6,index*6)
            console.log(index)
        },
        // 
        prevPage(index){
            // console.log(index)
        },
        // 
        nextPage(index){
            // console.log(index)
        },
        _getStuData(){
            this.axios.post('/admin/user/get_all_users',{b_id: this.classID}).then(res =>{
                res.data.data.forEach((ele,index) =>{
                    ele.serial = index+1
                })
                this.tableData = res.data.data
                this.pages =  Math.ceil(this.tableData.length*10/6);
                this.myTableData = this.tableData.slice(0,6)
            }).catch(err =>{
                console.log(err)
            })
        } 
    },
    created(){
        this._getStuData()
    }
}
</script>