<template>
    <div class="user-wrapper">
         <div class="user-box">
            <div class="search">
                <span></span>
                <div>
                   <div style="display inline-block; margin-left: 1vw">
                        <el-input v-model="className" placeholder="" @input="searchData" clearable></el-input>
                   </div>
                </div>
                <el-button type='primary' @click="addClass('add')" class="add-ident"></el-button>
            </div>
            <div class="table">
                <p class="table-title"></p>
                <el-table :data="myTableData" style="width: 100%">
                    <el-table-column prop="admin_name" label="" min-width="20px">
                    </el-table-column>
                    <el-table-column prop="admin_real" label="" min-width="20px">
                    </el-table-column>
                    <el-table-column prop="admin_limit_name" label="" min-width="20px" >
                    </el-table-column>
                    <el-table-column prop="admin_state" label="" min-width="20px" >
                        <template slot-scope="scope">
                            <i class="iconfont" :class="scope.row.admin_state == 1 ? 'green' : 'red'">
                                {{scope.row.admin_state == 1 ? '&#xe62e;' : '&#xe600;'}}
                            </i>
                        </template>
                    </el-table-column>
                    <el-table-column prop="create_time" label="" min-width="20px">
                    </el-table-column>
                    <el-table-column label="" min-width="20px">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="addClass('redact',scope.row,scope.$index)"><i class="iconfont">&#xe64b;</i></el-button>
                            <el-button type="text" size="small" @click="deleteData(scope.row)"><i class="iconfont">&#xe631;</i></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <el-dialog :title="disTitle" :visible.sync="dialogFormVisible" :close-on-click-modal='false'>
            <el-form :model="formData">
                <el-form-item label="" label-width="80px">
                    <el-input :disabled="isamend" v-model="formData.admin_name" placeholder="" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px">
                    <el-input type="password" v-model="formData.admin_pwd" :placeholder="passPlaceholder" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px">
                    <el-input v-model="formData.admin_real" placeholder="" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px">
                    <el-select v-model="formData.admin_limit_name" filterable placeholder="" @change='changeIdent'>
                        <el-option v-for="item in idents" :key="item.limit_name" :label="item.limit_name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" label-width="80px">  
                    <el-radio-group v-model="formData.admin_state">
                        <el-radio :label="1"></el-radio>
                        <el-radio :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="addClassTrue"> </el-button>
            </div>
        </el-dialog>
        <el-dialog
            title=""
            :visible.sync="dialogVisible2"
            width="30%">
            <span>?</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2 = false"> </el-button>
                <el-button type="primary" @click="deleteTrue"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<style scoped>
    @import './userAdmin.css';
</style>

<script>
export default {
    data(){
        return {
            value:'',
            id: 0,
            isAdd: true,
            className: '',
            isamend: false,
            formData:{
                admin_name: '',
                admin_pwd: '',
                admin_real: '',
                admin_limit_name: '',
                admin_state: 1
            },
            idents: [],
            disTitle: '',
            dialogFormVisible: false,
            dialogVisible2: false,
            tableData: [],
            myTableData: [],
            examinations: [],
            examination_mns: [],
            identID: 0,
            passPlaceholder: ''
        }
    },
    methods: {
        // /
        addClass(value,row,index){
            if(value == 'add'){
                this.disTitle = '';
                this.passPlaceholder = '';
                this.isamend = false;
                this.formData = { admin_name: '', admin_pwd: '', admin_real: '', admin_state: 1,admin_limit_name: ''};
                this.identID = 0;
                this.dialogFormVisible = true
            }else{
                this.id = row.id;
                this.axios.get('/admin/user/getAdminUser?id='+row.id).then(res =>{
                    this.disTitle = '';
                    this.passPlaceholder = ' ()';
                    this.isamend = true;
                    this.formData = res.data.data;
                    this.dialogFormVisible = true;
                    this.identID = res.data.data.admin_limit
                })
            }
        },
        // /
        addClassTrue(){
            if(this.disTitle == ''){
                const data = {
                    id: this.id,
                    admin_real: this.formData.admin_real,
                    admin_limit: this.identID,
                    admin_state: this.formData.admin_state
                }
                if(this.formData.admin_pwd) {
                    data.admin_pwd = this.formData.admin_pwd
                }
                this.axios.post('/admin/user/modifyAdminUser',data).then(res =>{
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    })
                    this._getInfoData()
                    this.dialogFormVisible = false;
                })
            }else{
                if(!this.formData.admin_name) return this.$message({message: '',type: 'error'});
                if(!this.formData.admin_pwd) return this.$message({message: '',type: 'error'});
                if(!this.formData.admin_real) return this.$message({message: '',type: 'error'});
                if(!this.formData.admin_limit_name) return this.$message({message: '',type: 'error'});
                const data = {
                    admin_name: this.formData.admin_name,
                    admin_pwd: this.formData.admin_pwd,
                    admin_real: this.formData.admin_real,
                    admin_limit: this.identID,
                    admin_state: this.formData.admin_state
                }
                this.axios.post('/admin/user/addAdminUser',data).then(res =>{
                    console.log(res)
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    })
                    this._getInfoData()
                    this.dialogFormVisible = false;
                })
            }
        },
        // 
        deleteData(row){
            this.id = row.id;
            this.dialogVisible2 = true;
        },
        // 
        deleteTrue(){
            this.axios.get('/admin/user/deleteAdminUser?id='+this.id).then(res =>{
                this.$message({
                        message: res.data.msg,
                        type: 'success'
                    })
                this.dialogVisible2 = false;
                this._getInfoData()
            })
        },
        changeIdent(e){
            this.identID = e;
        },
        // 
        searchData(value){
            let index = 0;
            this.myTableData = this.tableData.filter(item =>{
                return item.admin_real.match(value) || item.admin_limit_name.match(value)
            })
        },
        _getInfoData(){
            this.axios.get('/admin/user/AdminUserList').then(res =>{
                this.tableData = res.data.data;
                this.myTableData = this.tableData;
            })
            this.axios.get('/admin/user/getLimit').then(res =>{
                this.idents = res.data.data;
            })
        }
    },
    created(){
        this._getInfoData()
    }
}
</script>