<template>
    <div class="simu-wrapper">
        <div class="simu-box">
            <div class="search">
                <div class="condition">
                    <span></span>
                    <el-select v-model="value" placeholder="" @change="getValue">
                        <el-option 
                        v-for="item in options"
                        :key="item.value"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </div>
                <div>
                  <span class="topic-name"></span>
                  <el-input
                    placeholder=""
                    v-model="topicName"
                    @input="searchTopicName"
                    clearable>
                  </el-input>
                </div>
                <!-- <div class="time">
                    <span class="startDate"></span>
                    <div class="block start">
                        <el-date-picker
                        v-model="startDate"
                        type="datetime"
                        placeholder="">
                        </el-date-picker>
                    </div>
                    <span class="endDate"></span>
                    <div class="block">
                        <el-date-picker
                        v-model="endDate"
                        type="datetime"
                        placeholder="">
                        </el-date-picker>
                    </div>
                </div> -->
                <!-- <el-button type='primary' @click="searchData" class="el-icon-search"> </el-button> -->
            </div>
            <div class="table">
                <p class="table-title">{{title}}</p>
                <el-table :data="myTableData" style="width: 100%">
                    <el-table-column prop="serial" label="" min-width="10px">
                    </el-table-column>
                    <el-table-column prop="name" label="" min-width="30px">
                    </el-table-column>
                    <el-table-column v-if="isOper" prop="type" label="" min-width="10px">
                    </el-table-column>
                    <el-table-column prop="currentSelect" label="" min-width="10px">
                    </el-table-column>
                    <el-table-column prop="searchNum" label="" min-width="10px">
                    </el-table-column>
                    <el-table-column prop="usersNum" label="" min-width="10px">
                    </el-table-column>
                    <el-table-column prop="qual" label="" min-width="10px">
                    </el-table-column>
                    <el-table-column label="" min-width="36px">
                        <template slot-scope="scope">
                            <!-- <el-button @click="handleClick(scope.row)" type="text" size="small"><i class="iconfont">&#xe605;</i></el-button> -->
                            <el-button @click="redact(scope.row)" type="text" size="small"><i class="iconfont">&#xe64b;</i></el-button>
                            <el-button type="text" @click="deleteData(scope.row)" size="small"><i class="iconfont">&#xe631;</i></el-button>
                            <el-button @click="addTopic(scope.row)" type="text" size="small"><i class="iconfont">&#xe61b;</i>{{scope.row.currentTopic}}</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="paging">
                    <el-pagination
                        @current-change	='changePage'
                        background
                        layout="prev, pager, next"
                        :total="pages">
                    </el-pagination>
                </div>
            </div>
        </div>
        <!--  -->
        <el-dialog
          title=""
          :visible.sync="dialogVisible" :close-on-click-modal='false' width="30%">
          <span></span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="isDelete"> </el-button>
          </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogFormVisible2" :close-on-click-modal='false'>
          <el-form :model="formData" label-width="150px">
            <!-- label-width="50vw" -->
            <el-form-item label="" >
              <el-input v-model="formData.name" autocomplete="off" placeholder=""  label-width='30px'></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="formData.select">
                <el-option v-for="(item,index) in formData.selects" :key="index" :label="item" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" >
               <!-- <el-date-picker v-model="formData.date" type="date" placeholder=""></el-date-picker> -->
               <el-date-picker v-model="formData.date" type="datetime" placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item label="()" >
               <el-input v-model="formData.time" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" >
               <el-input v-model="formData.stuNum" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="formData.audit">
                <el-option v-for="(item,index) in formData.audits" :key="index" :label="item" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" label-width='240px'>
              <el-radio-group v-model="radio">
                <el-radio-button label=""></el-radio-button>
                <el-radio-button label=""></el-radio-button>
              </el-radio-group>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible2 = false"> </el-button>
            <el-button type="primary" @click="settingTopic"> </el-button>
          </div>
        </el-dialog>
        <!-- ABCD -->
        <el-dialog
          title=""
          :visible.sync="dialogVisible3" :close-on-click-modal='false' width="30%">
          <span class="dialogVisible3Text"></span>
          <el-select v-model="selectValue" placeholder="">
                        <el-option 
                        v-for="item in options2"
                        :key="item"
                        :value="item">
                        </el-option>
          </el-select>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible3 = false"> </el-button>
            <el-button type="primary" @click="goToSetTopic"> </el-button>
          </span>
        </el-dialog>
    </div>
</template>

<style scoped>
    @import './simu.css';
</style>

<script>
  export default {
    props: {
      isOper: {
        type: Boolean,
        required: true
      },
      title: {
        type: String,
        required: true
      }
    },
    data() {
      return {
        id: 0,
        pages: 0,
        radio: '',
        currentTopic: '',
        options: [{
          value: ''
        }, {
          value: ''
        }, {
          value: ''
        }],
        options2: [],
        topicName: '',
        selectValue: 'A',
        value: '',
        startDate: '',
        endDate: '',
        myTableData: [],
        tableData: [],
        formData: {
          name: '',
          select: '',
          selects: [],
          date: '',
          time: '',
          stuNum: '',
          audit: '',
          audits: ['','']
        },
        dialogVisible: false,
        dialogFormVisible2: false,
        dialogVisible3: false
      }
    },
    methods: {
        // 
        getValue(value){
            if(this.title == ''){
              if(value == ''){
                this.axios.get('/admin/issue/trueList?simulation=1&time=1').then(res =>{
                  this._getData(res.data.data)
                })
              }else if(value == ''){
                this.axios.get('/admin/issue/trueList?simulation=1&time=3').then(res =>{
                  this._getData(res.data.data)
                })
              }else if(value == ''){
                this.axios.get('/admin/issue/trueList?simulation=1').then(res =>{
                  this._getData(res.data.data)
                })
              }
            }else if(this.title == ''){
              if(value == ''){
                this.axios.get('/admin/issue/trueList?simulation=0&time=1').then(res =>{
                  this._getData(res.data.data)
                })
              }else if(value == ''){
                this.axios.get('/admin/issue/trueList?simulation=0&time=3').then(res =>{
                  this._getData(res.data.data)
                })
              }else if(value == ''){
                this.axios.get('/admin/issue/trueList?simulation=0').then(res =>{
                  this._getData(res.data.data)
                })
              }
            }else if(this.title == ''){
              if(value == ''){
                this.axios.get('/admin/issue/trueList?time=1').then(res =>{
                  this._getData(res.data.data)
                })
              }else if(value == ''){
                this.axios.get('/admin/issue/trueList?time=3').then(res =>{
                  this._getData(res.data.data)
                })
              }else if(value == ''){
                this.axios.get('/admin/issue/trueList').then(res =>{
                  this._getData(res.data.data)
                })
              }
            }
        },
        searchData(){

        },
        // 
        searchTopicName(){
          this.myTableData = this.tableData.filter(data =>{
            return data.name.match(this.topicName)
          })
        },
        // 
        handleClick(row) {
          this.id = row.id;
          const routerLink = this.$router.resolve({path: '/Topic',query: {id: row.id}});
          window.open(routerLink.href,'_blank')
        },
        // 
        addTopic(row){
          this.id = row.id
          this.axios.get('/admin/issue/abcdIssue?id='+this.id).then(res =>{
            console.log(res);
            const title = row.currentTopic;
            const id = this.id;
            const name = res.data.data.title;
            const outline = res.data.data.outline;
            this.$emit('addTopic',{title, id, outline, name})
          })
          
        },
        // 
        changePage(currentIndex){
            this.myTableData = this.tableData.slice(6*(currentIndex - 1),6*(currentIndex - 1) + 6)
        },
        // 
        redact(row){
          this.id = row.id;
          this.axios.get('/admin/issue/editIssue?id='+row.id).then(res =>{
            this.formData.name  = res.data.data.title;
            this.formData.date = res.data.data.kk_time;
            this.formData.time = res.data.data.kssc_time;
            this.formData.stuNum = res.data.data.cankao_num;
            this.formData.select = res.data.data.choice + '';
            this.formData.audit = res.data.data.examine == 0 ? '' : '';
            res.data.data.s_type.forEach((ele,index) =>{
              this.formData.selects[index] = ele + ''
            })
            this.options2 = this.formData.selects
          })
          this.dialogFormVisible2 = true;
        },
        // 
        settingTopic(){
          const data = {
            id: this.id,
            title: this.formData.name,
            kk_time: this.formData.date,
            kssc_time: this.formData.time,
            cankao_num: this.formData.stuNum,
            choice: this.formData.select.slice(0,1),
            examine: this.formData.audit == '' ? 0 : 1
          }
          this.axios.post('/admin/issue/editDoIssue',data).then(res =>{
            this._getInfoData()
            this.dialogFormVisible2 = false;
            this.$message({
              message: '',
              type: 'success'
            })
            if(this.radio == ''){
              this.dialogVisible3 = true;
            }
          })
        },
        // 
        goToSetTopic(){
          this.$emit('amendTopic',{id: this.id, s_type: this.selectValue.slice(0,1)})
          this.dialogVisible3 = false;
        },
        // 
        deleteData(row){
          this.id = row.id //idisDelete 
          this.dialogVisible = true;
        },
        // 
        isDelete(){
          this.dialogVisible = false;
          this.axios.post('/admin/issue/deleteIssue?id='+this.id).then(res =>{
            this.$message({
              message: '',
              type: 'success'
            })
            this._getInfoData()
          })
        },
        _getData(data){
            data.forEach((ele,index) =>{
                    this.tableData[index] = {};
                    this.tableData[index].id = ele.id;
                    this.tableData[index].currentTopic = ele.abc
                    this.tableData[index].serial = index+1;
                    this.tableData[index].name = ele.title;
                    this.tableData[index].type = ele.simulation;
                    this.tableData[index].currentSelect = ele.choice + '';
                    this.tableData[index].usersNum = ele.ks_num
                    this.tableData[index].searchNum = ele.j_num;
                    this.tableData[index].qual = ele.rate;
                    this.pages = this.tableData.length/6 * 10;
                    this.myTableData = this.tableData.slice(0,6);
                })
        },
        _getInfoData(){
          if(this.title == ''){
              this.axios.get('/admin/issue/trueList?simulation=1').then(res =>{
                this._getData(res.data.data)
              })
          }else if(this.title == ''){
              this.axios.get('/admin/issue/trueList?simulation=0').then(res =>{
                this._getData(res.data.data)
              })
          }else if(this.title == ''){
              this.axios.get('/admin/issue/trueList').then(res =>{
                this._getData(res.data.data)
              })
          }
          
        }
    },
    created(){
      this._getInfoData()
    }
  }
</script>